@use "../base/variables" as var;
.sesion{
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin-top: -5rem;
    p{
        font-size:14px;
        color:white;
    }
}
.nav-cita{
    display:grid;
    grid-template-columns: repeat(3,1fr);
}
.nav-cita__boton{
    font-size: 14px;
    text-align: center;
    background-color:var.$azul ;
    padding: 1rem 2rem;
    font-weight: bold;
    &:hover{
        background-color: darken(var.$azul,20%);
    }
}
.main-servicios{
    display:none;
}
.contenedor-servicios{
        display:grid;
        grid-template-columns: repeat(2,1fr);
        column-gap: 1rem;
        text-align: center;

}
.servicios{
    background-color: white;
    padding:2rem;
    border-radius: 8px;
    transition-duration: 300ms;
    &.seleccionado{
        background-color: var.$azul;
    }
    p{
        margin:0;
        
    }
    .servicios__nombre{
        color:black;
        font-size: 16px;
        margin-bottom: 1rem;
    }
    .servicios__precio{
        font-weight: bold;
        color:var.$azul;
        font-size: 24px;
        &.blanco{
            color:white;
        }
    }
    &:hover{
        transform: scale(1.1);
    }
}
.informacion-cita{
    display:none;
}
.resumen{
    display:none;
}

.mostrar{
    display:block;
}
.nover{
    visibility: hidden;
}
.botones-paginas{
    display:flex;
    justify-content: space-between;
    
}
.cambiarBlanco{
    background-color: white;
    &:hover{
        background-color: white;
    }
    
}
