@use "../base/mixins" as mix;
.formulario{
    display:flex;
    flex-direction: column;
    row-gap: 1rem;

    .campos{
        display:flex;
        flex-direction: column;
        align-items: center;
        
    
        input{
            padding:1rem;
            border-radius: 1rem;
        }
        
        @include mix.tablet{
            flex-direction: row;
            label{
                flex-basis: 15%;
            }
            input:not([type="submit"]){
                flex-basis: 85%;
            }


        }

    }
    input[type="submit"]{
        display:inline;
        width: fit-content;
    }
    .input-nombre{
        color:white;
    }

}