/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #e1e1e1;
  font-size: 14px;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.boton-azul {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: white;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton-azul {
    width: auto;
  }
}
.boton-azul:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.boton-naranja {
  background-color: orange;
  padding: 1.5rem 4rem;
  color: white;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton-naranja {
    width: auto;
  }
}
.boton-naranja:hover {
  background-color: #cc8400;
  cursor: pointer;
}

.boton-rojo {
  background-color: red;
  padding: 1.5rem 4rem;
  color: white;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton-rojo {
    width: auto;
  }
}
.boton-rojo:hover {
  background-color: #cc0000;
  cursor: pointer;
}

.alerta {
  color: white;
  text-align: center;
  margin: 1rem 0;
  padding: 1rem 0;
  display: block;
  width: 100%;
}
.alerta.error {
  background-color: red;
}
.alerta.correcta {
  background-color: green;
}

.main-container {
  width: 100%;
  height: 100%;
}
@media (min-width: 768px) {
  .main-container {
    display: flex;
    height: 100%;
  }
}

.background-barber {
  flex-basis: 50%;
  height: 20rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url("../img/1.jpg");
}
@media (min-width: 768px) {
  .background-barber {
    height: 100%;
  }
}

.information-container {
  flex-basis: 50%;
  height: 100%;
}

.formulario {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
}
.formulario .campos {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.formulario .campos input {
  padding: 1rem;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .formulario .campos {
    flex-direction: row;
  }
  .formulario .campos label {
    flex-basis: 15%;
  }
  .formulario .campos input:not([type=submit]) {
    flex-basis: 85%;
  }
}
.formulario input[type=submit] {
  display: inline;
  width: fit-content;
}
.formulario .input-nombre {
  color: white;
}

.contenedor-login h2 {
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .contenedor-login {
    margin-top: 6rem;
  }
}

.inicia-sesion {
  margin: 5rem 0;
  text-align: center;
  color: #e1e1e1;
}

.login-links {
  display: flex;
  justify-content: space-between;
}

.sesion {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: -5rem;
}
.sesion p {
  font-size: 14px;
  color: white;
}

.nav-cita {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.nav-cita__boton {
  font-size: 14px;
  text-align: center;
  background-color: #0da6f3;
  padding: 1rem 2rem;
  font-weight: bold;
}
.nav-cita__boton:hover {
  background-color: #076493;
}

.main-servicios {
  display: none;
}

.contenedor-servicios {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 1rem;
  text-align: center;
}

.servicios {
  background-color: white;
  padding: 2rem;
  border-radius: 8px;
  transition-duration: 300ms;
}
.servicios.seleccionado {
  background-color: #0da6f3;
}
.servicios p {
  margin: 0;
}
.servicios .servicios__nombre {
  color: black;
  font-size: 16px;
  margin-bottom: 1rem;
}
.servicios .servicios__precio {
  font-weight: bold;
  color: #0da6f3;
  font-size: 24px;
}
.servicios .servicios__precio.blanco {
  color: white;
}
.servicios:hover {
  transform: scale(1.1);
}

.informacion-cita {
  display: none;
}

.resumen {
  display: none;
}

.mostrar {
  display: block;
}

.nover {
  visibility: hidden;
}

.botones-paginas {
  display: flex;
  justify-content: space-between;
}

.cambiarBlanco {
  background-color: white;
}
.cambiarBlanco:hover {
  background-color: white;
}

.contenedor-servicio {
  border-bottom: 1px solid white;
}
.contenedor-servicio span {
  color: rgb(156, 50, 192);
}

.admin-navegacion {
  display: flex;
  align-items: center;
  justify-content: center;
}
.admin-navegacion a {
  flex-basis: 33.3%;
  font-size: 14px;
}

.admin-citas h3 {
  text-align: left;
}

.admin-cita p span, .admin-servicio p span {
  color: blue;
}

.admin-servicio {
  border-bottom: 1px solid white;
  border-top: 1px solid white;
}

.admin-servicios ul {
  margin-top: 3rem;
}

.servicios-botones {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 4rem;
}
.servicios-botones input, .servicios-botones a {
  margin: 1rem 0;
}
.servicios-botones input {
  width: 100%;
}