@use "../base/mixins" as mix;
.main-container{
    width: 100%;
    height: 100%;
    @include mix.tablet{
        display:flex;
        height: 100%;
    }
}
.background-barber{
    flex-basis: 50%;
    height: 20rem;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    background-image: url("../img/1.jpg");
    @include mix.tablet{
        height: 100%;
    }
}
.information-container{
    flex-basis: 50%;
    height: 100%;
}

